.header {
    background-color: var(--main-color);
    box-shadow: 0px 4px 10px rgba(97, 162, 58, 0.2), 0px 4px 50px rgba(94, 236, 54, 0.5), 0px 4px 4px rgba(17, 168, 26, 0.2);

    &__desktop {
        display: flex;
        align-items: center;
        padding: 40px;
        z-index: -1;
    }

    &__logo {
        font-family: var(--main-color);
        font-weight: 700;
        font-size: 24px;
        line-height: 1.16;
        text-align: center;
        max-width: 118px;
        margin-right: 136px;
        color: var(--text-color);
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

        @include tablet {
            margin-right: 161px;
            max-width: 156px;
        }

        @include desktop {
            margin-right: 335px;
            max-width: 121px;
        }
    }

    &__nav {
        @media screen and (max-width: 1199px) {
            display: none;
        }
    }

    &__menu {
        display: flex;
        gap: 48px;
    }

    &__menu-item {
        display: flex;
        gap: 10px;

    }

    &__menu-link {
        display: block;
        position: relative;
        font-family: var(--main-font);
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        color: var(--text-color);
        padding-bottom: 8px;

        &:hover,
        &:focus {
            border-bottom: 1px solid var(--accent-color);
            padding-bottom: 7px;
        }

    }

    &__btn {
        display: block;
        margin-left: auto;
        padding: 16px 20px;
        background-color: var(--accent-color);
        border-radius: 16px;
        font-family: var(--main-font);
        font-weight: 700;
        font-size: 14px;
        line-height: 100%;
        text-align: center;
        cursor: pointer;
        color: #000000;

        &:hover,
        &:focus {
            box-shadow: 0px 8px 20px rgba(134, 229, 67, 0.5), 0px 4px 10px rgba(255, 255, 255, 0.1);
        }

        @media screen and (max-width: 1199px) {
            margin-left: 222px;
        }

        @media screen and (max-width: 767px) {
            display: none;

        }
    }
}